
export let plugin:$pocketsPlugin = {
    async init(app: any, $pockets: $pockets){

        var res:any = null
        let connection = async (...args: any) => {
            if(!res) {
                let module = await import('./create-connection')
                let { connect } = module.createConnection({
                    url: "/wp-json/pockets/v1/connection"
                })
                res = connect
            }
            return await res(...args)
        }

        $pockets.connection = connection
        
    },
}