import { reactive } from "vue"
import { $pockets } from "@/pockets"
export let store = {
    $data: reactive({}),
    create(k: string, v: any) {
        if( this.$data.hasOwnProperty(k) ) return $pockets.debug.log('Store Already Exists!', k, v)
        this.$data[k] = v
    },
    use(k: string) {
        if( !this.$data.hasOwnProperty(k) ) return $pockets.debug.log('Store Does Not Exist!', k)
        return this.$data[k] 
    }
} 
