import { defineAsyncComponent } from "vue"
import  router  from "./router"
let component = defineAsyncComponent( () => import("./component/index.vue") );

let installer = (app: $pocketsApp, $pockets: $pockets) => {
    app.use( router )
    app.component('pockets-documentation', component )
}
export let plugin:$pocketsPlugin = {
    init(app: any, $pockets: $pockets) {
        $pockets.documentation.add( {
            title: "My Component",
            type: "component",
        } )
        $pockets.customElement.create( 'pockets-documentation', installer)
    },
}
 